          <p class="ivu-mt-8">{{ $t('compute[1]') }} <img :src="logo" class="" v-width="8"> Eth :</p>
          <InputNumber class="numberInput" :step=0.1 :precision=2 :min="INVEST_MIN_AMOUNT"
                       v-model="value1"></InputNumber>
          <p class="ivu-mt-8">{{$t('compute[2]')}} (%):</p>
