</template>

<script>
import mixins from './mixins'
import moment from 'moment'
import bigNumber from 'bignumber.js'


export default {
  name: 'Ether',
  mixins: [mixins],
  computed: {
    rowShow: function () {
      let outs = new bigNumber(this.value1).multipliedBy(2);
      let comArr = [];
      let rate = new bigNumber(this.value2).plus(1);
      let a = 0;
      for (let i = 0; i < 100; i++) {
        let b = rate.multipliedBy(this.value1).dividedBy(100);
        if (b.plus(a) >= outs) {
          a = outs;
          comArr.push({
            index: i + 1,
            rate: rate,
            at: a
          })
          break;
        } else {
          a = b.plus(a);
          comArr.push({
            index: i + 1,
            rate: rate,
            at: a
          })
          rate =rate.plus(0.1);
        }
      }
      return comArr
    },
    linksUrl: function () {
      return window.location.origin + '?ref=' + this.account;
    },
    ref: function () {
      let re = this.getQueryVariable('ref');
      if (!re) re = '0x0000000000000000000000000000000000000000';
      return re;
    },
    lastRegTime: function () {
      let a = 0;
      if (this.userLastInvestInfo) a = moment(this.userLastInvestInfo[2] * 1000).format('YYYY-MM-DD hh:mm');
      return a;
    }
  },
  filters: {
    miniAddress(e, s, l) {
      if (e) {
        return e.substring(0, s) + '...' + e.substring(e.length - l)
      } else {
        return ''
      }
    }
  },
  async mounted() {
    await this.$store.dispatch('initWeb3');
    await this.setInterFace();
  },
  methods: {
    language(locale) {
      this.$i18n.locale = locale
    },
    invest() {
      this.$set(this, 'popBox', true);
      this.$set(this, 'investVisible', true);
      this.$set(this, 'popTitle', this.$t('navMenu[0]'));
    },
    myInfo() {
      this.$set(this, 'popBox', true);
      this.$set(this, 'myInfoVisible', true);
      this.$set(this, 'popTitle', this.$t('navMenu[1]'));
    },
    compute() {
      this.$set(this, 'popBox', true);
      this.$set(this, 'computeVisible', true);
      this.$set(this, 'popTitle', this.$t('navMenu[2]'));
    },
    closePopBox() {
      this.$set(this, 'popBox', false);
      this.$set(this, 'computeVisible', false);
      this.$set(this, 'myInfoVisible', false);
      this.$set(this, 'investVisible', false);
      this.$set(this, 'popTitle', '');
    },
    handleCopy() {
      this.$Copy({
        text: this.linksUrl
      })
    },
    investNow() {
      let loadMsg = null;
      this.contract.methods.invest(this.ref).send({
        from: this.account,
        gas: 3000000,
        value: new bigNumber(this.investAmount).multipliedBy(10**18)
      }).on('transactionHash', async hash => {
        loadMsg = this.$Message.loading({
          content: this.$t('state[0]'),
          duration: 0
        });
      }).on('receipt', async receipt => {
        setTimeout(loadMsg, 10);
        this.$Message.success(this.$t('state[1]'))
        this.setInterFace()
      }).on('error', (error) => {
        console.log(error)
        this.$Message.error(this.$t('state[2]'))
      })
    },
    withdraw() {
      if (!this.userAvailable) return;
      let loadMsg = null;
      this.contract.methods.withdraw().send({
        from: this.account,
        gas: 3000000,
        value: 0
      }).on('transactionHash', async hash => {
        loadMsg = this.$Message.loading({
          content: this.$t('state[0]'),
          duration: 0
        });
      }).on('receipt', async receipt => {
        setTimeout(loadMsg, 10);
        this.$Message.success(this.$t('state[3]'))
        this.setInterFace()
      }).on('error', () => {
        this.$Message.error(this.$t('state[4]'))
      })
    },
    getQueryVariable(variable) {
      let query = window.location.search.substring(1);
      let vars = query.split("&");
      for (let i = 0; i < vars.length; i++) {
        let pair = vars[i].split("=");
        if (pair[0] == variable) return pair[1];
      }
      return false;
    }
  }
}
</script>