import Vue from 'vue'
import Vuex from 'vuex'
import Web3 from 'web3'
import contractInfo from '../libs/contract'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    net:1,
    account:null,
    balance:0,
    contract:null,
  },
  getters:{
    net: state => state.net,
    account: state => state.account,
    balance: state => parseFloat(state.balance),
    contract: state => state.contract,
  },
  mutations: {
    SET_ACCOUNT(state,account){
      state.account = account
    },
    SET_BALANCE(state,balance){
      state.balance=balance
    },
    SET_NETWORK(state,net){
      state.net=net
    },
    SET_CONTRACT(state,contract){
      state.contract = contract
    }
  },
  actions: {
    async initWeb3({commit}) {
      if(typeof window.ethereum !== "undefined"){
        const ethereum = window.ethereum
        ethereum.autoRefreshOnNetworkChange = false
        try {
          let accounts = await ethereum.enable()
          window.web3 = new Web3(Web3.givenProvider);
          let account = accounts[0];
          let balance = await window.web3.eth.getBalance(account);

          const contract = new web3.eth.Contract(contractInfo.abi,contractInfo.address);

          commit('SET_CONTRACT',contract)
					commit('SET_ACCOUNT',account)
          commit('SET_BALANCE',web3.utils.fromWei(balance,'ether'))
          commit('SET_NETWORK',Number(ethereum.networkVersion))

          ethereum.on('accountsChanged',async accounts=>{
            account = accounts[0];
            balance = await web3.eth.getBalance(account)
            commit('SET_ACCOUNT',account)
            commit('SET_BALANCE',web3.utils.fromWei(balance,'ether'))
          })
          ethereum.on('networkChanged',async netWork=> {
            balance = await web3.eth.getBalance(account)
            commit('SET_BALANCE',web3.utils.fromWei(balance,'ether'))
            commit('SET_NETWORK',Number(ethereum.networkVersion))
          })
        }catch (e) {
          console.log('Not setup MetaMask !')
        }
      }
    }
  },
  modules: {
  }
})